.container {
  margin-top: 24px;
}

.form {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.label {
  font-weight: bold;
}

.input {
  padding: 12px;

  border: 1px solid #ccc;
  border-radius: 4px;
}

.btn {
  padding: 12px 24px;

  background-color: #007bff;

  color: #fff;

  border: none;
  border-radius: 4px;
  cursor: pointer;
}
